}

.shop-cart-button {
  float: right;
  cursor: pointer;
  transition: color, 500ms ease, -webkit-transform;
  transition: color, transform, 500ms ease;
  transition: color, transform, 500ms ease, -webkit-transform;
  padding-top: 14px;
  margin-left: 20px;
  margin-right: 40px;
  margin-bottom: 12px;
  font-size: 30px;
  color: #ffffff;

}

.shop-cart-button:hover,
.shop-cart-button:active {
  color: #ffffff;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.shop-cart {
  position: absolute;
  top: 90px;
  right: 18px;
  width: 500px;
  min-height: 100px;
  background: #cacaca;
  box-shadow: 5px 4px 10px 3px rgba(0, 0, 0, 0.77);

}

.cart {
  padding-left: 195px;
}


.nav-menu-bar {
  padding: 12px 0px 0px 0px;
  font-size: 33px;

}

.logo {
  padding: 10px;
  padding-left: 170px;
  padding-right: 175px;
